@mixin w {
  @media only screen and (max-width: "1439px") {
    @content; } }
@mixin x {
  @media only screen and (max-width: "1365px") {
    @content; } }
@mixin d {
  @media only screen and (max-width: "1199px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin a {
  @media only screen and (max-width: "666px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');
@mixin f {
  font-family: 'Poppins', sans-serif; }

$wh: #fff;
$bl: #000;
$gray: #868E9C;
$dark: #203152;
$blue: #1297FF;

html {
  // height: 100%
  &.no-scroll {
    overflow: hidden; } }

body {
  min-width: 375px;
  @include f;
  font-size: 18px;
  line-height: (26/18);
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $dark;
  @include m {
    font-size: 16px; }
  &.no-scroll {
    overflow: hidden; } }

.page {
  overflow: hidden; }

.center {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px; }

a.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.btn {
  min-width: 190px;
  height: 40px;
  border-radius: 20px;
  padding: 0 30px;
  @include f;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: all .25s;
  &_border {
    border: 1px solid $gray;
    color: $dark; }
  &_dark {
    background: #203152;
    box-shadow: 0 8px 8px 0 rgba(0,0,0,0.10);
    color: $wh; }
  &_gradient {
    background-image: linear-gradient(270deg, #78EE85 25%, $blue 75%);
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 200% auto;
    box-shadow: 0 2px 4px 0 rgba(18,213,116,0.20), 0 4px 8px 0 rgba(0,0,0,0.10);
    color: $wh;
    &:hover {
      background-position: 30% 50%; } }
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 10px 0 rgba(0,0,0,0.10); }
  &:active {
    transform: translateY(2px);
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.10); } }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  padding: 48px 0;
  @include d {
    padding: 36px 0; }
  @include m {
    padding: 26px 0; }
  &__center {
    display: flex;
    align-items: center;
    max-width: 1790px; }
  &__logo {
    margin-right: 133px;
    font-size: 0;
    @include d {
      margin-right: auto; }
    @include t {
      margin-right: auto; }
    @include m {
      position: relative;
      z-index: 20; } }
  &__logo &__pic {
    width: 174px;
    @include m {
      width: 160px; } }
  &__wrap {
    display: flex;
    align-items: center;
    flex-grow: 1;
    @include t {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      width: 350px;
      min-height: 100vh;
      padding: 100px 50px 50px;
      background: rgba($wh,.98);
      transform: translateX(100%);
      transition: transform .25s;
      &.visible {
        transform: translateX(0); } }
    @include m {
      display: block;
      width: 100%;
      min-height: 100%;
      padding-top: 120px;
      box-shadow: 0 10px 20px 0 rgba(0,0,0,0.10);
      text-align: center; } }
  &__nav {
    display: flex;
    margin: 0 auto;
    @include t {
      flex-direction: column;
      width: 100%;
      margin: 0 0 auto; }
    @include m {
      align-items: center;
      margin-bottom: 20px; }
    @include s {
      margin-bottom: 40px; } }
  &__link {
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 1.5;
    font-weight: 400;
    text-transform: uppercase;
    color: $dark;
    opacity: .5;
    transition: opacity .25s;
    @include t {
      font-size: 20px;
      opacity: 1; }
    &:hover {
      opacity: 1; }
    @include nl {
      margin-right: 48px;
      @include d {
        margin-right: 35px; }
      @include t {
        margin: 0 0 50px; }
      @include m {
        margin-bottom: 32px; } } }
  &__btns {
    display: flex;
    @include t {
      display: block; } }
  &__btn {
    @include t {
      min-width: 100%; }
    @include nl {
      margin-right: 16px;
      @include t {
        margin: 0 0 20px; } }
    &.btn_border {
      min-width: auto;
      @include t {
        min-width: 100%; } } }
  &__burger {
    display: none;
    position: relative;
    z-index: 12;
    width: 30px;
    height: 30px;
    background: none;
    font-size: 0;
    transition: opacity .25s;
    @include t {
      display: inline-block; }
    &:before,
    &:after {
      content: "";
      display: inline-block;
      width: 24px;
      height: 2px;
      background: $dark;
      transition: transform .25s; }
    &:before {
      margin-bottom: 2.5px; }
    &:after {
      margin-top: 2.5px; }
    &:hover {
      opacity: .85; }
    &.active {
      &:before {
        transform: translateY(3.5px) rotate(45deg); }
      &:after {
        transform: translateY(-3.5px) rotate(-45deg); } } }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 8;
    background: rgba(#000, .7);
    visibility: hidden;
    opacity: 0;
    transition: all .25s;
    @include m {
      display: none; }
    &.show {
      @include t {
        visibility: visible;
        opacity: 1; } } } }

.main {
  position: relative;
  margin-bottom: 67px;
  background: url('../img/bg-main.svg') no-repeat 0 100% / auto 100%;
  @include rmin(1920) {
    background-size: 100% auto; }
  @include m {
    padding-bottom: 430px; }
  @include s {
