    @include m {
      margin-bottom: 30px;
      font-size: 18px; } }
  &__btn {
    margin-bottom: 20px;
    .btn {
      min-width: 270px;
      height: 64px;
      margin-bottom: 20px;
      border-radius: 32px;
      font-size: 16px;
      @include m {
        min-width: 100%;
        height: 48px;
        border-radius: 24px;
        font-size: 14px; } } }
  &__note {
    max-width: 270px;
    text-align: center;
    font-size: 12px;
    line-height: (17/12);
    color: $gray;
    @include m {
      max-width: 100%; }
    strong {
      font-weight: 600; } }
  &__browser,
  &__phone {
    position: absolute;
    font-size: 0;
    opacity: 0; }
  &__browser {
    top: 184px;
    right: calc(50% - 1167px);
    box-shadow: 0 10px 20px rgba($bl, .1);
    border-radius: 10px;
    width: 1028px;
    animation: imageBigLeft 1s forwards;
    @include r(1599) {
      right: calc(50% - 1000px);
      width: 900px; }
    @include w {
      top: 150px; }
    @include d {
      right: calc(50% - 850px);
      width: 750px; }
    @include t {
      right: calc(50% - 900px); }
    @include m {
      top: auto;
      right: auto;
      left: 100px;
      bottom: 0;
      width: 615px; } }
  &__phone {
    top: 340px;
    right: calc(50% - 290px);
    width: 314px;
    animation: imageSmLeft 1.5s .75s forwards;
    @include r(1599) {
      top: 290px;
      right: calc(50% - 240px);
      width: 290px; }
    @include w {
      top: 260px; }
    @include d {
      top: 230px;
      width: 250px; }
    @include t {
      right: calc(50% - 300px); }
    @include m {
      top: auto;
      left: 0;
      bottom: 15px;
      width: 190px; } }
  &__pic {
    width: 100%;
    @include m {
      max-height: 430px; } } }

@keyframes imageBigLeft {
  0% {
    transform: translateX(30%);
    opacity: 0; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes imageSmLeft {
  0% {
    transform: translateX(50%);
    opacity: 0; }
  30% {
    opacity: 1; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

