.owl-nav {
  display: flex;
  justify-content: center; }

.owl-carousel .owl-nav button.owl-next,
.owl-carousel .owl-nav button.owl-prev {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid rgba($gray, .3);
  font-size: 0;
  transition: all .25s;
  .icon {
    font-size: 18px;
    fill: $dark;
    transition: fill .25s; }
  &:hover {
    background: $dark;
    border-color: $dark;
    .icon {
      fill: $wh; } }
  &.disabled {
    opacity: .5;
    pointer-events: none; } }

.owl-carousel .owl-nav button.owl-prev {
  .icon {
    margin-right: 2px; } }

.owl-carousel .owl-nav button.owl-next {
  .icon {
    margin-left: 2px; } }

.owl-dots {
